.formio-component-select .choices[data-type=select-one]:after
{
  content: url('../img/pin.png');
  top: 20%;
  border: none;
  margin: 0;
}

@media only screen and (max-width: 768px)
{
  .formio-form .choices__item.choices__item--selectable {
      font-size: 14px;
      opacity: 1;
  }
}

.formio-form .choices__item.choices__item--selectable {
  white-space: nowrap;
  overflow: inherit!important;
  padding-right: 25px;
  text-overflow: ellipsis;
  color: #000;
  font-size: 16px;
  font-weight: 600;
  opacity: 1;
}


.tab {
  overflow: hidden;
  background-color: white;

}

.tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 18px 25px 18px 25px;
  transition: 0.3s;
  font-size: 15px;
}

.tab button.active {
    border: 2px solid rgb(32, 74, 214);
    background-color: rgb(253, 252, 252);
}

.tabcontent {
  display: none;
  padding: 6px 12px;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 2px 2px rgb(199, 199, 199);
}

.search {
  width: 100%;
  border: 1px solid #494b4b;
  position: relative;
  display: flex;
  border-radius: 4px;
}

.searchTerm {
  width: 100%;
  border: 0px;
  padding: 5px;
  height: auto;
  outline: none;
  border-right: 1px solid grey;
}

.searchButton {
  width: 40px;
  height: 40px;
  border: 0px;
  text-align: center;
  background-color: white;
  font-size: 25px;
  border-radius: 4px;
}

